/* settings.css */
.settings-container {
  max-width: 500px;
  margin: 3rem auto;
  padding: 2rem;
  background-color: #f9f9f9;
  border: 1px solid #2ecc71;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(46, 204, 113, 0.3);
  font-family: Arial, sans-serif;
}

.settings-container h2 {
  color: #2ecc71;
  text-align: center;
  margin-bottom: 2rem;
  font-weight: bold;
}

.settings-form label {
  display: block;
  margin-bottom: 1rem;
  font-weight: bold;
  color: #333;
}

.settings-form input[type="text"],
.settings-form input[type="email"],
.settings-form input[type="password"] {
  width: 100%;
  padding: 0.6rem;
  margin-top: 0.25rem;
  border: 1px solid #2ecc71;
  border-radius: 4px;
  font-size: 1rem;
  box-sizing: border-box;
}

.settings-form input[disabled] {
  background-color: #e0e0e0;
  color: #555;
  cursor: not-allowed;
}

.checkbox-label {
  display: flex;
  align-items: center;
  font-weight: bold;
  margin-bottom: 1rem;
  cursor: pointer;
  user-select: none;
  color: #2ecc71;
}

.checkbox-label input {
  margin-right: 0.5rem;
  cursor: pointer;
}

.buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 1.5rem;
}

.btn {
  padding: 0.6rem 1.5rem;
  border-radius: 20px;
  border: none;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-size: 1rem;
}

.save-btn {
  background-color: #2ecc71;
  color: white;
}

.save-btn:hover {
  background-color: #27ae60;
}

.delete-btn {
  background-color: #e74c3c;
  color: white;
}

.delete-btn:hover {
  background-color: #c0392b;
}

.message {
  margin-top: 1rem;
  font-weight: bold;
  text-align: center;
  color: #e74c3c;
}
